<template>
  <view class="main-container">
    <CardType
      :bg="'https://cdn-static.papa.com.cn/social/home-bg5.png'"
      :titleInfo="{ ...titleInfo, num: count }"
      @clickMore="handleClickMore"
    >
      <template #list>
        <view class="list-container">
          <view
            class="item"
            v-for="item in list"
            :key="item.instructor_id"
            @click="handleClickItem(item)"
          >
            <view class="name">{{ item.name }}</view>
            <view class="tag">{{ item.tag_ids_str }}</view>
            <view class="avatar-container">
              <view class="avatar">
                <image class="img" :src="item.avatar || defaultImg" alt="" mode="aspectFill" />
              </view>
              <view class="medal">
                <levelIcon :level="item.level"></levelIcon>
              </view>
            </view>
          </view>
          <view v-if="list.length == 0">
            <empty :marginTop="0"></empty>
          </view>
        </view>
      </template>
    </CardType>
  </view>
</template>

<script>
import { getWxInstructorList } from '@/apis/home/index'
import CardType from './CardType.vue'
import DefaultAvatar from '@/static/images/avatar.png'
import rankIcon from '@/components/LevelIcon/rank.vue'
import levelIcon from '@/components/LevelIcon/index.vue'
export default {
  components: {
    CardType,
    rankIcon,
    levelIcon,
  },
  props: {
    list: {
      type: Array,
      default: () => {
        return []
      },
    },
    count: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      defaultImg: DefaultAvatar,
      titleInfo: {
        title: '社体指导员',
        num: this.count,
        unit: '个',
        color: '#F47E15',
      },
      list: [],
      count: 0,
    }
  },
  created() {
    this.getInstructorList()
  },
  methods: {
    handleClickMore() {
      uni.switchTab({
        url: '/pages/tabbar/commonPage/index',
      })
    },
    async getInstructorList() {
      let res = await getWxInstructorList({ page: 1, size: 5 })
      if (res.code == 200) {
        this.list = res.data.list
        this.count = res.data.count
      } else {
        this.$toast(res.message)
      }
    },
    handleClickItem(item) {
      uni.navigateTo({
        url: `/pages-sub/instructorDetail/index?instructor_id=${item.instructor_id}`,
      })
    },
  },
}
</script>

